div.operate_area {
    position:fixed;
    top: 0px;
    height: 10em;
    width: 100%;

    text-align: left;
    line-height: 1em;

    padding: 10px 10px;
    background: lightblue;

    z-index: 1;
}
div.operate_area .operate_btns {
    display: inline-block;
}
div.operate_area .operate_btn {
    padding: 5px;
}

div.intro_area {
    padding-top: 5em;
}

div.edit_area {
    margin-top: 1em;
    text-align: left;
}
div.edit_area h1 {
    font-size: 1em;
}
div.edit_area h2 {
    font-size: 1em;
    padding-left: 2em;
}
div.edit_area .sub_edit_area {
    margin-left: 4em;
    border: black solid 1px;
}

.title_summary_part {
    display: flex;
    min-height: 5em;
}
.title_summary_part input {
    width: 24em;
}
.title_summary_part textarea {
    flex-grow: 1;
}

.fundamental_event_summary .input_area {
    display: flex;
    min-height: 6em;
}
.fundamental_event_summary .input_area .left {
    display:inline-block;
    width: 25em;
}
.fundamental_event_summary .input_area .right {
    flex-grow: 1;
}

.forex_trend_summary {
    padding: 2px;
}
.forex_trend_summary table {
    width: 100%;
}
.forex_trend_summary table tr th, table tr td {
    border: 1px solid black;
}

.forex_position_summary {
    display: flex;
    align-items: center;

    padding: 10px;
}
.forex_position_summary .left {

}
.forex_position_summary .center {
    flex: 1;
    padding: 10px;
}
.forex_position_summary .right {
    width: 10%;
    text-align: center;
}

.major_currency_forecast .input_area{
    display: flex;
    align-items: center;
    text-align: center;
}
.major_currency_forecast .input_area .left {
    display:inline-block;
    width: 10%;
}
.major_currency_forecast .input_area .center {
    flex: 1;
}
.major_currency_forecast .input_area .right {
    width: 20%
}

.option_strategy {
    display: flex;
    min-height: 15em;
}
.option_strategy .left {
    flex: 1;

}
.option_strategy .right {
    width: 300px;
}

.future_data_event {
    padding: 5px;
}


.result_area {
    text-align: left;
}